import {Languages} from '/js/util';

interface SelOption {
	value: string | number;
	text: string;
	children?: SelOption[]
}

class FloorSelector {
	picker :PopPickerClass ;
	button : HTMLElement;
	constructor(floorArr: SelOption[], buttonId: string) {

		this.picker = new mui.PopPicker({
			buttons: [Languages.package.cancel, Languages.package.ok]
		});
		this.button = document.getElementById(buttonId);
		this.picker.setData(floorArr);
		this.button.innerText = floorArr[0].text;

	}
}

export {SelOption , FloorSelector};